/* purgecss start ignore */

.lc-pie-chart-legend {
  font-size: 14px;
  line-height: 1.5;
  padding: 32px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lc-pie-chart-legend-label-name {
      font-weight: bold;
    }

.lc-pie-chart-legend-label-name::after {
        content: ':';
      }

.lc-pie-chart-legend-label-percentage::before {
        content: '(';
      }

.lc-pie-chart-legend-label-percentage::after {
        content: ')';
      }

.lc-pie-chart-legend-item {
    display: inline-flex;
    align-items: center;
    margin-right: 24px;
  }

.lc-pie-chart-legend-item:last-child {
      margin-right: 0;
    }

.lc-pie-chart-legend-color {
    display: inline-block;
    border-radius: 100%;
    height: 11px;
    width: 11px;
    margin-right: 8px;
  }

/* purgecss end ignore */
